import firebase from '../Firebase'



const updateNote = async (collection, id, data) => {
    const db = firebase.firestore();
    let auxData = { ...data };
    delete auxData['id'];
    await db.collection(collection).doc(id).set(auxData);
}

const deleteNote = async (collection, id) => {
    const db = firebase.firestore();
    await db.collection(collection).doc(id).delete();
}


const createNote = async (collection, data) => {
    const db = firebase.firestore();
    console.log(data);
    await db.collection(collection).add(data);
}

const createUser = async (e, email, password) => {
    console.log(email)
    e.preventDefault();
    await firebase.auth().createUserWithEmailAndPassword(email, password);
}


const signin = async (e, email, password) => {
    e.preventDefault();
    let response;
    await firebase.auth().signInWithEmailAndPassword(email, password).then(cred => response = true).catch(err => response = false);
    return response;
}

const onClickSaveBtn = async (e, emailState, passwordState, history) => {
    e.preventDefault();
    let res = await signin(e, emailState, passwordState);
    if(res) {
        history.push('/')
    }
}


export { updateNote, deleteNote, createNote, createUser, signin, onClickSaveBtn }

