import React, { useState } from 'react'
import styles from './Login.module.css'
import { signin, onClickSaveBtn } from '../../functions/functions'
import { useHistory } from "react-router-dom";

const Login = () => {
    let [emailState, setEmailState] = useState();
    let [passwordState, setPasswordState] = useState();
    const history = useHistory();

    return (
        <form>
            <input value={emailState} onChange={e => setEmailState(e.target.value)} placeholder='Insert your e-mail...'></input>
            <input value={passwordState} type='password' onChange={e => setPasswordState(e.target.value)} placeholder='Insert your password...'></input>
            <button onClick={(e) => {
                onClickSaveBtn(e, emailState, passwordState, history);
            }}>ENTER</button>
        </form>
    )
}

export default Login