import React, { useEffect } from "react";
import { useState } from "react";
import firebase from './Firebase'

export const Context = React.createContext();

export const Provider = (props) => {
  let [editWindowIsVisible, setEditWindowIsVisible] = useState(false);
  let [firestoreData, setFirestoreData] = useState([])
  let [currentNote, setCurrentNote] = useState({});
  let [createFlag, setCreateFlag] = useState();
  let [currentUser, setCurrentUser] = useState();

  const fetchData = async () => {
    const db = firebase.firestore();
    const data = await db.collection('NONG').get();
    setFirestoreData(data.docs.map(doc => ({ ...doc.data(), id: doc.id })));
  }

  useEffect(() => {
    fetchData();
  }, [editWindowIsVisible])

  useEffect(() => {
    firebase.auth().onAuthStateChanged(setCurrentUser);
  }, [])

  return (
    <Context.Provider value={{ editWindowIsVisible, setEditWindowIsVisible, firestoreData, setFirestoreData, currentNote, setCurrentNote, createFlag, setCreateFlag, currentUser }}>
      {props.children}
    </Context.Provider>
  );
};

export default Provider;
